#main {
  margin-top: 60px;
}

#about {
  height: calc(100vh - 60px);
  height: calc(100dvh - 60px);
  display: flex;
  align-items: center;
}

#particlesBG canvas {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  pointer-events: none;
  opacity: 0.1;
}

v-timeline {
  display: none;
  transition: all 5s ease;
}

.v-timeline {
  display: flex !important;
  flex-direction: column;
}

.v-timeline-item {
  display: grid !important;
  grid-template-columns: 1fr 30px 1fr;
  grid-template-rows: 200px;
  align-items: center;
}
.v-timeline-item.visible .v-timeline-item__opposite,
.v-timeline-item.visible .v-timeline-item__body {
  opacity: 1;
}
.v-timeline-item .v-timeline-item__opposite,
.v-timeline-item .v-timeline-item__body {
  opacity: 0;
  transition: all 3s ease;
}
.v-timeline-item > * {
  grid-row: 1 !important;
}

.v-timeline-item__body {
  display: flex;
  align-items: center;
}